import React from "react";
import PageHeader from "components/PageHeader";
import Title from "components/Title";
import Orders from "./Orders";

import './style.scss';
export default function CheckoutPage() {

    const submitFormHandler = (e) => {
        e.preventDefault();

        console.log('Xin chúc mừng Hoàn đẹp zai đã mua 100 chiếc Iphone 14 Pro Max');
    }
    return (
        <main>
            <section className="clear--padding">
                <div className="container">
                    <PageHeader title='Checkout' slug='' last='Checkout' />
                </div>
            </section>
            <section>
                <div className="container">
                    <div className="gap"></div>
                    <Title title='BILLING DETAILS' />
                    <div className="gap--10"></div>
                    <div className="flex flex--gap checkout-page">
                        <div className="col-sm-12 col-lg-8">
                            <form onSubmit={submitFormHandler}>
                                <div>
                                    <label className="uppercase">Full name</label>
                                    <input type="text" className="input" placeholder="Enter your full name here!" />
                                </div>
                                <div className="gap--20"></div>
                                <div>
                                    <label className="uppercase">Email</label>
                                    <input type="text" className="input" placeholder="Enter your email here!" />
                                </div>
                                <div className="gap--20"></div>
                                <div>
                                    <label className="uppercase">Phone Number</label>
                                    <input type="text" className="input" placeholder="Enter your phone numbers here!" />
                                </div>
                                <div className="gap--20"></div>
                                <div>
                                    <label className="uppercase">Adress</label>
                                    <input type="text" className="input" placeholder="Enter your adress here!" />
                                </div>
                                <div className="gap--20"></div>
                                <div>
                                    <button className="btn black pi uppercase">
                                        Place order
                                    </button>
                                </div>
                            </form>
                        </div>
                        <div className="col-sm-12 col-lg-4">
                            <Orders />
                        </div>
                    </div>
                    <div className="gap--50"></div>
                </div>
            </section>
        </main>
    )
}