import { useSelector } from "react-redux"

export default function Orders() {
    const cart = useSelector(state => state.cart);
    return (
        <div className="bg-light pd pi--30">
            <h5>YOUR ORDER</h5>
            <div className="gap--10"></div>
            <ul className="alt">
                {cart.items.length > 0 && (
                    cart.items.map((p, i) => {
                        return (
                            <li className="flex x--between y--center" key={i}>
                                <div><h6><small>{p.name}</small></h6></div>
                                <div><small>{new Intl.NumberFormat("de-DE").format(p.price) + 'đ'} * {p.amount}</small></div>
                            </li>
                        )
                    })
                )}
                <li className="divider"></li>
                <li className="flex x--between y--center">
                    <div><h6>TOTAL</h6></div>
                    <div><h6>{new Intl.NumberFormat("de-DE").format(cart.totalPay) + ' VNĐ'}</h6></div>
                </li>
            </ul>
            <div className="gap"></div>
        </div>
    )
}